.services-card {
    width: 20%; height: 90%;
    background-image: linear-gradient(to right bottom,  #4b4b4b, #363636, #272727e5);
    align-items: center;
    text-align: center;

    margin: 0 1rem;
    padding: 0;
    
    z-index: 1;
    border-radius: 0.5rem;
}
.services-card:hover {
    background-image: linear-gradient(to right bottom, #242424af, #3a3939, #242323e5);
    cursor: pointer;
}
.services-card-content {
    width: 100%; height: 85%;
    font-family: 'Libre Franklin';
    padding: 0 1rem;
}
.header-card {
    height: 25%;
    color: rgb(167, 167, 167);
}

.header-card img {
    width: 25%;
    padding: 25% 0 0 0 ;
    margin-left: auto;
    margin-right: auto;
}


.body-card {
    height: 55%;
    padding-top: 2rem;
    padding-bottom: 2rem;
}
.body-card h3 {
    height: 12vh;
    font-size: 22px;
    font-weight: 400;
    color: rgb(253, 253, 253);
    line-height: 1;
    margin-bottom: 2rem;
}
.body-card div h4 {
    font-size: 16px;
    font-family: 'Libre Franklin';
    font-weight: 400;
    color: rgb(131, 131, 131);
}
.body-card .price {
    font-size: 40px;
    font-weight: 600;
    color: rgb(255, 255, 255);
    padding-top: 1rem;
}

.body-card h3 b {
    font-weight: 500;
}

.button {
    color: rgb(255, 255, 255);
    font-size: 16px;
    font-weight: 400;
    background-color: rgb(117, 117, 117);
    padding: 0.5rem 2rem;
    width: 100%;
}


@media only screen and (min-width: 250px) and (max-width: 720px) {
    .services-card {
        width: 98%; height: 12rem;
        padding-top: 0.5rem;
        margin-top: 1.5rem;
        margin-bottom: 2rem;
        margin-left: auto;
        margin-right: auto;
    }
    .services-card:hover{}
    .services-card :active {
    }
    .services-card-content {
        height: 8rem;
        padding: 0;
        margin-bottom: 1rem;
    }
    .header-card {
        height: 30%;
        
    }
    .header-card img {
        width: 12%;
        padding: 0.5rem 0 0 0;
        margin-bottom: 1rem;
        margin-left: auto;
        margin-right: auto;
    }
    

    .body-card {
        height: 60%;
        padding-top: 1rem;
    }
    .body-card h3 {
        font-size: 20px;
        font-weight: 400;
        height: 8vh;
        margin-top: 1rem;
        letter-spacing: 1px;
        color: white;
    }
    .body-card div {
        display: none;
        justify-content: space-evenly;
        align-items: center;
        flex-wrap: wrap;
    }
    .body-card div h4 {
        font-size: 16px;
        font-weight: 300;
        margin: 0.2rem 0.5rem;
    }

    .button {
        width:100%; height: 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        font-weight: 600;
        color: rgb(255, 255, 255);
        background-color: rgb(144, 144, 144);
    }
}

@media only screen and (min-width: 720px) and (max-width: 1024px) {

    .body-card h3 {
        font-size: 18px;
        height: 8vh;
        margin-bottom: 0;
    }
    .body-card div h4 {
        font-size: 12px;
        font-weight: 300;
    }
    .button {
        font-size: 15px;
    }
}