.text-nodo h1 {
    font-family: 'Libre Franklin';
    overflow-wrap: break-word;
    color: rgb(249, 247, 255);
    font-size: 25px;
    
}
.text-nodo h1 b{
    color: rgba(240, 45, 110, 0.87);
}

@media only screen and (max-width: 370px) {
    .text-nodo h1 {
        font-size: 35px;
    }
}
@media only screen and (max-width: 720px) {
    
    .text-nodo {
        height: 600px;
        padding: 2rem 0 0 0;
    }
    .text-nodo h1{
        text-align: start;
        padding: 7rem 1rem 30rem;
        font-size: 30px;
        line-height: 0.9;
        font-weight: 700;
    }
    .text-nodo h2{
        height: 30vh;
        font-family: 'Poppins';
        text-align: center;
        padding: 1.5rem;
        font-size: 24px;
        font-weight: 500;
        color: rgb(249, 247, 255);
        letter-spacing: 1.5px;
    }
}
@media only screen and (min-width: 365px) and (max-width: 720px) {
    .text-nodo {
        height: 100%;
    }
    .text-nodo h1 {
        text-align: start;
        margin-top: 2rem;
        padding-right: 2rem;
        font-size: 34px;
        font-weight: 600;
        padding-bottom: 38rem;
    }
}

@media only screen and (min-width: 720px) and (max-width: 1024px) {
    .text-nodo {
        width: 100%; height: 100%;
        padding: 32vh 0% 0% 47%;
    }
    .text-nodo h1{
        font-weight: 800;
        font-size: 45px;
        line-height: 38px;
        padding: 0 2rem 0 0;
    }
    .text-nodo h2{
        font-weight: 300;
        text-align: left;
        font-size: 22px;
        line-height: 1;
        padding: 0.5rem 2rem 0 0;
        color: rgb(214, 214, 214);
    }
  }
    
  @media only screen and (min-width: 1025px) {
    .text-nodo {
        width: 100%; height: 100%;
        padding: 34vh 0% 5% 42%;
    }
    .text-nodo h1{
        font-weight: 600;
        font-size: 46px;
        line-height: 40px;
        padding: 0 5rem 0 0;
    }
    .text-nodo h2{
        padding: 0.5rem 8rem 0 0;
        font-weight: 300;
        text-align: left;
        font-size: 30px;
        line-height: 1;
        color: rgb(214, 214, 214);
    }
  }