*{
    box-sizing: border-box;
}
.social-nav {
    display: none;
    width: 5rem; height: 2.5rem;
    position: fixed;
    top: 38rem;
    background-color: rgb(219, 219, 219);
    display: flex;
    justify-content: end;
    align-items: center;
    z-index: 1000000;
}
.social-nav a {
    padding: 0rem 1rem;
}
.social-nav a img {
    max-width: 1.7rem;
}
.social-nav a img:hover {
    width: 1.7rem;
}
.social-nav a .fb-img {
    max-width: 1.7rem;
}
@media only screen and (max-width: 410px) {
    .social-nav {
        display: none;
        width: 5rem; height: 2.7rem;
        position: fixed;
        top:600px;
        background-color: rgb(219, 219, 219);
        display: flex;
        justify-content: end;
        align-items: center;
    }
    .social-nav a img {
        max-width: 1.8rem;
    }
}
@media only screen and (min-width: 410px) and (max-width: 720px) {
    .social-nav {
        display: none;
        width: 5rem; height: 2.7rem;
        position: fixed;
        top:800px;
        background-color: rgb(219, 219, 219);
        display: flex;
        justify-content: end;
        align-items: center;
    }
    .social-nav a img {
        max-width: 1.8rem;
    }
}
@media only screen and (min-width: 1023px) and (max-width: 1200px) {
    .social-nav {
        display: none;
        width: 5rem; height: 2.5rem;
        position: fixed;
        top:32rem;
        background-color: rgb(219, 219, 219);
        display: flex;
        justify-content: end;
        align-items: center;
    }
    .social-nav a img {
        max-width: 1.5rem;
    }
}