
.carousel {
    height: 680px;
    padding-top: 4rem;
    z-index: 0;
}
.carousel-control-prev {
}
.carousel-inner {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    height: 525px;
    padding-top: 8rem;
}
.carousel-item .item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4rem;
    color: white;
}
.item img{
    max-width: 18rem;
}
.item .text {
    padding-left: 5rem;
    text-align:start;
    font-family: 'Poppins';
}
.text h2 {
    font-size: 28px;
    font-weight: 400;
}
.text h3 {
    font-size: 18px;
    font-weight: 200;
    letter-spacing: 1.2px;
}

.text .line {
    height: 0.5rem; width: 14rem;
    margin-bottom: 1rem;
}

@media only screen and (min-width: 361px) and (max-width: 1023px) {
    .carousel{
        height: 830px;
    }
    .carousel-inner {
        padding-top: 3.5rem;
        height: 100%;
    }
    .carousel-item .item {
        display: block;
        text-align: center;
        padding: 0rem 0.5rem;
    }
    .item img{
        max-width: 18rem;
    }
    .item .text {
        height: 20rem;
        margin-top: 2rem;
        padding: 0;
        text-align: center;
    }
    .text h2 {
        font-size: 28px;
        font-weight: 600;
        margin-bottom: 1rem;
    }
    .text h3 {

        font-size: 22px;
        font-weight: 100;
    }
    
    .text .line {
        height: 0.5rem; width: 10rem;
        margin-bottom: 1rem;
    }

    .carousel-control-prev {
        display: none;
    }
    .carousel-control-next {
        display: none;
    }
}

@media only screen and (max-width: 360px){
    #services{
        margin-top: 2rem;
        height: 700px;
        margin-top: 2rem;
        padding: 4rem 0 0 0;
    }
    .carousel-inner {
        height: 749px;
        padding-top: 3rem;
    }
    .carousel-item .item {
        display: block;
    }

    .carousel-item .item {
        text-align: center;
        padding: 0rem 0.5rem;
    }
    .item img{
        max-height: 12rem;
    }

    .item .text {
        margin-top: 2rem;
        height: 20rem;
        padding: 0;
        text-align: center;

    }
    .text h2 {
        font-size: 23px;
        padding: 1rem 0 0.5rem 0;
        font-weight: 700;
    }
    .text h3 {
        height: 20rem;
        font-size: 17px;
        font-weight: 200;
        
    }
    .carousel-control-prev {
        display: none;
    }
    .carousel-control-next {
        display: none;
    }
}
