.services-header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: auto;
    margin-left: auto;
    background-image: linear-gradient(to right bottom, #242424, #424242e5);
    border-radius: 25px;
    margin-top: 1rem;
}
.header-options {
    display: flex;
    align-items: center;
}
.header-options .header-icon {
    font-size: 30px;
}
.header-options h3 {
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 1.2px;
    padding: 2rem;
    color: white;
    margin: 0;
}
.selected {
    color: rgb(155, 132, 158);
}
.header-options:hover {
    cursor: pointer;
}
.header-icon {
    color: white;
    font-size: 22px;
    cursor: pointer;
}
 

@media only screen and (min-width: 360px) and (max-width: 720px) {
    .services-header {
        width: 90%;
    }
    .services-header .header-icon {
        font-size: 28px;
    }
    .header-options h3 {
        font-size: 22px;
        font-weight: 200;
        letter-spacing: 1.2px;
    }
}    
@media only screen and (min-width: 576px) and (max-width: 1500px) {
    .services-header {
        width: 40%;
    }

}    

@media only screen and (min-width: 750px) and (max-width: 1024px) {
    .header-options {
        font-size: 15px;
        
    }

} 