@media only screen and (max-width: 570px) {
    .popup-background {
        display: flex;
        align-items: center;
        justify-content: center;
        position:fixed;
        top: 0;
        background-color: rgba(0, 0, 0, 0.63);
        background-image: linear-gradient(to right bottom, #0c0c0caf, #1d1d1d, #252525, #181717e5);
        z-index: 100;
        width: 100vw; height: 100%;
        color: white;
    }
    .popup-container {
        width: 98%; height: 90%;
        padding: 0.8rem;
        border-radius: 0.5rem;
    }
  
    .popup-header .popup-icon:hover {
        color: rgb(255, 255, 255);
        font-size: 45px;;
    }
    .popup-header a {
        display: flex;
        align-items: center;
        text-decoration: none;
        justify-content: center;
        width: 8rem; height: 3rem;
        background-color: rgb(2, 1, 2);
        border-radius: 0.5rem;
        cursor: pointer;
      
    }
    .popup-header a p {
        margin: 0;
        color: white;
    }
    .popup-header a:hover {
        background-color: rgb(109, 109, 109);
    }
    
    .popup-container .popup-content {
        height: 40%; width: 100%;
        justify-content: center;
        align-items: center;
        margin-top: 2rem;
        margin-bottom: 1.5rem;
    }
    .popup-container .popup-content img{
        display: none;
    }
    .popup-container .popup-content .popup-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .popup-container .popup-content .popup-title h2 {
        font-size: 22px;
        font-weight: 600;
        padding: 0.5rem;
        padding-top: 0;
    }
    .popup-container .popup-content div h2 {
        font-size: 20px;
        padding: 1rem;
        padding-top: 0;
    }
    .popup-container .popup-content .popup-title a {
        text-align: center;
        text-decoration: none;
        font-size: 16px;
        font-weight: 600;
        letter-spacing: 1px;
        background-color: rgb(165, 52, 95);
        color: rgb(0, 0, 0);
        border-radius: 0.5rem;
        padding: 0.5rem;
    }
    .popup-container .popup-content {
        height: 55%;
        margin-bottom: 2rem;
    }
    .popup-container .popup-content h2 {
        font-size: 20px;
        font-weight: 400;
        margin-top: 0.5rem;
    }
    
    
    .popup-detail {
        width: 100%;
        height: 32%;    
        display: flex;
        overflow-y: scroll;
        overflow-x: hidden;
    }
    .popup-detail .detail {
        padding: 0rem 1rem;
        width: 50%;
    }
    .popup-detail .detail h3 {
        font-size: 20px;
    }
    
    .graphic {
        display: flex; 
    }
    .graphic-table {
        display: flex;
        width: 20rem; height: 12rem;
    }
    .graphic-table .level{
        text-align: center;
        width: 4rem; height: 100%;
        margin:0rem 0.5rem;
    }
    .graphic-table .level-bar{
        width: 100%; height: 100%;
        padding: 0rem 0.5rem;
    }
    .graphic-table .level .level-bar .graph-bar{
        position: relative;
        background-color: rgb(79, 48, 94);
        width: 100%;
    }
    .graphic-table .level-title{
        margin-top: 1rem;
        font-size: 14px;
        font-weight: 400;
        
    }
    
    .ruler {
        text-align: center;
        height: 12rem;
    }
    .ruler p{
        margin: 0;
    }
    .ruler .line {
        display: block;
        margin: 0rem 1rem;
        background-color: black;
        width: 1px; height: 80%;
    }
    
    .utilities {
        
        height: 100%;
    }
    .utilities-list {
        display: flex;
        flex-wrap: wrap;
    }
    .utilities-list div {
        margin: 0.5rem;
        padding: 0.5rem 0.5rem;
        border: solid 2px rgb(88, 88, 88);
        border-radius: 0.5rem;
    }
    .features-list {
        margin-top: 1rem;
    }
    .features-list div{
        font-size: 18px; 
        margin: 0.3rem 0rem;
    }
}

