        background-color: rgb(2, 1, 2);
        border-radius: 0.5rem;
        cursor: pointer;

    }
    .popup-header a:hover {
        background-color: rgb(98, 98, 98);
    }
    .popup-header a p {
        font-size: 1.2rem;
        font-weight: 600;
        color: rgb(199, 199, 199);
        margin: 0;
    }
    .popup-header .popup-icon:hover {
        color: rgb(255, 255, 255);
        margin-bottom: 2rem;
    }

    
    .popup-container .popup-content {
        height: 25%; width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 3rem;
    }
    .popup-container .popup-content img{
        max-width: 150px;   
        margin-left: 3rem;
        margin-right: 2rem;
    }
    .popup-container .popup-content div {
        width: 75%;
        align-items: center;
        justify-content: space-between;
    }
    .popup-container .popup-content div a {
        text-align: center;
        text-decoration: none;
        margin-bottom: 0.5rem;
        font-size: 12px;
        font-weight: 600;
        letter-spacing: 1px;
        background-color: rgb(163, 69, 155);
        color: rgb(0, 0, 0);
        border-radius: 0.5rem;
        padding: 0.5rem;
    }
    .popup-container .popup-content div a:hover {
        background-color: rgb(238, 79, 225);
    }
    .popup-container .popup-content div h1 {
        font-size: 24px;
    }
    .popup-container .popup-content div h2 {
        font-size: 16px;
        font-weight: 200;
        letter-spacing: 1px;
        width: 85%;
    }
    
    
    .popup-detail {
        padding: 2rem;
        display: flex;
        justify-content: space-evenly;
    }
    
    .graphic {
        display: flex;
    }
    .graphic-table {
        display: flex;
        width: 20rem; height: 10rem;
    }
    .graphic-table .level{
        text-align: center;
        width: 6rem; height: 100%;
        margin:0rem 0.5rem;
    }
    .graphic-table .level-bar{
        width: 100%; height: 100%;
        padding: 0rem 0.5rem;
    }
    .graphic-table .level .level-bar .graph-bar{
        position: relative;
        background-color: rgb(79, 48, 94);
        width: 100%;
    }
    
    .utilities {
        height: 100%;
        margin-left: 1rem;
    }
    .utilities h3{
        font-size: 18px;

    }
    .utilities-list {
        display: flex;
        flex-wrap: wrap;
    }
    .utilities-list div {
        font-size: 14px;
        margin: 0.5rem;
        padding: 0.5rem 0.5rem;
        border: solid 2px rgb(88, 88, 88);
        border-radius: 0.5rem;
    }
    
    
    .features h3 {
        font-size: 18px;
    }
    .features-list {
        margin-top: 1rem;
    }
    .features-list div{
        font-size: 14px; 
        margin: 0.3rem 0rem;
    }
}
